//  OwnerAPI 
import OwnerAPI from "../api";
// Request 
import { AxiosPatch } from "@/web/request";

/**
 *  , 
 */
const logIn = (payload) => {
    return new Promise((resolve, reject) => AxiosPatch(OwnerAPI.logIn, { "data": payload }).then(({ data }) => resolve(data)).catch(err => reject(err)));
};

/**
 *  or 
 * -- 
 * - logIn  ,  
 */
export { logIn };
export default { logIn };


